import { ReactElement } from 'react';
import { BrowserRouter as Router, Route, Redirect, Switch } from 'react-router-dom';
import DatasetsView from '../views/Datasets';
import DatasetView from '../views/Dataset';
import HomeView from '../views/Home';
import Header from '../components/common/Header';
import Footer from '../components/common/Footer';

export default function AppRouter(): ReactElement {
    return (
      <Router>
        <Header/>
        <Switch>
          <Route path="/vizgard/datasets" component={DatasetsView}/>
          <Route path="/vizgard/dataset" component={DatasetView}/>
          <Route path="/vizgard/" component={HomeView}/>
          <Redirect to="/vizgard/"/>
        </Switch>
        <Footer/>
      </Router>
    );
}
